@use 'constants';
iframe.crossword-iframe {
  height: constants.$crossword-height;
  width: 100%;
}

.container {
  display: none;
}

.container-active {
  margin-top: 20px;
  display: flex;
  gap: 1rem;
  justify-content: space-around;
}

#crosswordGrid {
  border: 2px solid constants.$fg-color;
  padding: 1rem;
}

.grid-row {
    display: flex;
}

.grid-cell {
    width: 30px;
    height: 30px;
    border: 1px solid constants.$bg-color;
    // border-bottom: 2px solid constants.$bg-color;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: constants.$hn-color;
}

.grid-cell.black {
    background: constants.$bg-color;
    // width: 31px;
    // border-right: 1px solid rgba(constants.$fg-color, 0.01);
    // border-bottom: 1px solid rgba(constants.$fg-color, 0.01);
}

.grid-cell input {
    width: 100%;
    height: 100%;
    border: none;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    background: transparent;
}

.grid-cell input.correct {
    background-color: #d4edda;
    color: #155724;
}

.grid-cell input.incorrect {
    background-color: #f8d7da;
    color: #721c24;
}

.grid-cell input.revealed {
    background-color: #fff3cd;
    color: #856404;
}

.cell-number {
    position: absolute;
    top: 1px;
    left: 2px;
    font-size: 10px;
    font-weight: bold;
    color: #333;
}

.hints-panel {
  display: flex;
  flex-direction: column;
}

.hints-section {
    margin-bottom: 20px;
}

.controls {
    text-align: center;
    margin-bottom: 20px;
}

.controls input[type="file"] {
    margin-right: 10px;
}

.btn {
    background: constants.$emph-color;
    color: constants.$bg-color;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    margin: 0 5px;
    font-family: constants.$font;
    font-weight: bold;
    font-size: 0.8rem;
}

.btn:hover {
    color: constants.$fg-color;
}

.error {
    color: red;
    text-align: center;
    padding: 10px;
}

.success {
    color: green;
    text-align: center;
    padding: 10px;
}
